/* -----------------------------NAV--------------------------------- */
#burger,
#label-menu {
  position: fixed;
  top: 14px;
  right: 20px;
  height: 20px;
  width: 20px;
  z-index: 1; }
  #burger .nav-line,
  #label-menu .nav-line {
    background-color: black;
    position: absolute;
    width: 100%;
    height: 2px;
    top: 50%;
    border-radius: 5px;
    -webkit-transition: 1s;
    transition: 1s; }
  #burger .nav-line:first-child,
  #label-menu .nav-line:first-child {
    top: 3px; }
  #burger .nav-line:last-child,
  #label-menu .nav-line:last-child {
    top: 16px; }

#burger:checked + #label-menu .nav-line {
  opacity: 0;
  top: 50%; }

#burger:checked + #label-menu .nav-line:first-child {
  opacity: 1;
  -webkit-transform: rotate(405deg);
          transform: rotate(405deg); }

#burger:checked + #label-menu .nav-line:last-child {
  opacity: 1;
  -webkit-transform: rotate(-405deg);
          transform: rotate(-405deg); }

input[type="checkbox"] {
  visibility: hidden; }

/* -----------------------------NAV--------------------------------- */
